#main-div {
  background-color: whitesmoke;
  width: 100%;
}
#id1 {
  padding-left: 2%;
  padding-top: 2%;
  background-color: white;
  height: 100vh;
}
#id2 {
  margin-bottom: 15%;
  padding-bottom: 4%;
  border-bottom: 2px solid grey;
}
span {
  color: orange;
  font-weight: bold;
}
.comp-1 {
  cursor: pointer;
  padding: 20px;
}
.comp-1:hover {
  color: blue;
  background-color: whitesmoke;
}
#id3 {
  padding-top: 10%;
}
.comp-2 {
  margin-left: 7%;
  background-color: white;
  width: 90%;
  height: 100px;
  display: flex;
  padding-top: 4%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  padding-left: 15%;
  border-radius: 20px;
}
.comp-2 h4 {
  font-weight: bold;
}
#id4 {
  margin-right: 30px;
  color: lightblue;
  background-color: rgb(255, 47, 123);
  padding: 10px;
  width: 20%;
  height: 50px;
  border-radius: 50%;
}
#id5 {
  margin-right: 20px;
}
@media only screen and (max-width: 700px) {
  #main-div {
    background-color: whitesmoke;
    width: 100%;
  }
  #id1 {
    padding-left: 2%;
    padding-top: 5%;
    background-color: white;
    height: 80vh;
  }
  #id4 {
    margin-right: 30px;
    color: lightskyblue;
    background-color: rgb(255, 47, 123);
    padding: 10px;
    width: 20%;
    height: 50px;
    border-radius: 50%;
  }
  .comp-2 {
    margin-left: 0%;
    background-color: white;
    width: 100%;
    height: 100px;
    display: flex;
    padding-top: 4%;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
      rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    padding-left: 15%;
    border-radius: 20px;
  }
}
@media (min-width: 768px) and (max-width: 1050px){
           .comp-2 {
  margin-left: 7%;
  background-color: white;
  width: 90%;
  height: 100px;
  display: flex;
  padding-top: 4%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  border-radius: 20px;
  padding-left: 5%;
}
        }
